import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import d3 from 'd3';
import s from './Contact.css';

class Contact extends React.Component {
  static propTypes = {
    title: PropTypes.string.isRequired,
  };

  initialize() {
    console.log(d3);
    let margin = { top: 20, right: 40, bottom: 30, left: 20 },
      width = 960 - margin.left - margin.right,
      height = 500 - margin.top - margin.bottom,
      barWidth = Math.floor(width / 19) - 1;

    const x = d3.scale.linear()
      .range([barWidth / 2, width - barWidth / 2]);

    const y = d3.scale.linear()
      .range([height, 0]);

    const yAxis = d3.svg.axis()
      .scale(y)
      .orient('right')
      .tickSize(-width)
      .tickFormat(d => `${Math.round(d / 1e6)}M`);

// An SVG element with a bottom-right origin.
    const svg = d3.select('body').append('svg')
      .attr('class', s.svg)
      .attr('width', width + margin.left + margin.right)
      .attr('height', height + margin.top + margin.bottom)
      .append('g')
      .attr('transform', `translate(${margin.left},${margin.top})`);

// A sliding container to hold the bars by birthyear.
    const birthyears = svg.append('g')
      .attr('class', s.birthyear);

// A label for the current year.
    const title = svg.append('text')
      .attr('class', s.title)
      .attr('dy', '.71em')
      .text(2000);

    d3.csv('/population.csv', (error, data) => {
      // Convert strings to numbers.
      data.forEach((d) => {
        d.people = +d.people;
        d.year = +d.year;
        d.age = +d.age;
      });

      // Compute the extent of the data set in age and years.
      let age1 = d3.max(data, d => d.age),
        year0 = d3.min(data, d => d.year),
        year1 = d3.max(data, d => d.year),
        year = year1;

      // Update the scale domains.
      x.domain([year1 - age1, year1]);
      y.domain([0, d3.max(data, d => d.people)]);

      // Produce a map from year and birthyear to [male, female].
      data = d3.nest()
        .key(d => d.year)
        .key(d => d.year - d.age)
        .rollup(v => v.map(d => d.people))
        .map(data);

      // Add an axis to show the population values.
      svg.append('g')
        .attr('class', s.y)
        .attr('transform', `translate(${width},0)`)
        .call(yAxis)
        .selectAll('g')
        .filter(value => !value)
        .classed('zero', true);

      // Add labeled rects for each birthyear (so that no enter or exit is required).
      const birthyear = birthyears.selectAll('.birthyear')
        .data(d3.range(year0 - age1, year1 + 1, 5))
        .enter().append('g')
        .attr('class', s.birthyear)
        .attr('transform', birthyear => `translate(${x(birthyear)},0)`);

      birthyear.selectAll('rect')
        .data(birthyear => data[year][birthyear] || [0, 0])
        .enter().append('rect')
        .attr('x', -barWidth / 2)
        .attr('width', barWidth)
        .attr('y', y)
        .attr('height', value => height - y(value));

      // Add labels to show birthyear.
      birthyear.append('text')
        .attr('y', height - 4)
        .text(birthyear => birthyear);

      // Add labels to show age (separate; not animated).
      svg.selectAll('.age')
        .data(d3.range(0, age1 + 1, 5))
        .enter().append('text')
        .attr('class', s.age)
        .attr('x', age => x(year - age))
        .attr('y', height + 4)
        .attr('dy', '.71em')
        .text(age => age);

      // Allow the arrow keys to change the displayed year.
      window.focus();
      d3.select(window).on('keydown', () => {
        switch (d3.event.keyCode) {
          case 37: year = Math.max(year0, year - 10); break;
          case 39: year = Math.min(year1, year + 10); break;
        }
        update();
      });

      function update() {
        if (!(year in data)) return;
        title.text(year);

        birthyears.transition()
          .duration(750)
          .attr('transform', `translate(${x(year1) - x(year)},0)`);

        birthyear.selectAll('rect')
          .data(birthyear => data[year][birthyear] || [0, 0])
          .transition()
          .duration(750)
          .attr('y', y)
          .attr('height', value => height - y(value));
      }
    });
  }

  handleClick = (e) => {
    e.preventDefault();
    this.initialize();
  }


  render() {
    return (
      <div className={s.root}>
        <div className={s.container}>
          <h1>{this.props.title}</h1>
          <p>...</p>
          <a onClick={e => this.handleClick(e)}>draw</a>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(Contact);



