import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Login.css';

class Login extends React.Component {
  static propTypes = {
    title: PropTypes.string.isRequired,
  };

  constructor(props) {
    super(props);
    this.state = {
      userName:'',
      userPassword: '',
      inRemeber: false,
      unameHelp: '',
      upwdHlpe: '',
    };
  }

  componentDidMount() {
    if(localStorage.loginStatus){
      this.setState({
        userName:JSON.parse(localStorage.loginStatus).userName||''
      })
    }
  }

  // 监听input中的数据，保存到state中
  changeUsername(e) {
    const uname = e.target.value;
    this.setState({
      userName: uname,
    });
    console.log(this.state.userName);
  }
  changePassword(e) {
    const upwd = e.target.value;
    this.setState({
      userPassword: upwd,
    });
  }
  // 是否记住密码
  handleCheckbox(e) {
    const isChecked = e.target.checked;
    if (isChecked) {
      this.setState({
        isRemember: true,
      });
    } else {
      this.setState({
        isRemember: false,
      });
    }
  }
  //点击登录按钮，触发后台接口提供的验证，对数据的处理等方法
  handleClick(){
    if(this.state.userName === ""||this.state.userName === null){
      this.setState({
        unameHelp: "* 用户名不能为空"
      })
    }else if(this.state.userPassword === ""||this.state.userPassword === null){
      this.setState({
        unameHelp: "",
        upwdHelp: "* 密码不能为空"
      })
    }else{
      this.setState({ //清除help-block提示文字
        unameHelp: "",
        upwdHelp: ""
      });

      if(this.state.isRemember === true){ //是否记住密码，若记住，则保存至localstorage，反之，清除
        // let loginData = {this.state.userName,this.state.userPassword};
        let loginData = {};
        loginData.userName = this.state.userName;
        loginData.userPassword = this.state.userPassword;
        console.log('remember');
        localStorage['loginStatus'] = JSON.stringify(loginData);

        // Data.localSetItem("loginStatus",loginData);
      }else{
        console.log('not remember');
        localStorage.removeItem('loginStatus');
        // Data.localRemoveItem("loginStatus");
      }

      // this.props.login(this.state.userName,this.state.userPassword);
      console.log(this.state);
    }
  }
  //显示账号.00000000000003333333333333333333333333
0

  render(){
        return (
          <div className="login-box">
            ​
            <div className="login-title">登   录</div>
            <form action="" className="form-horizontal">
              <div className="form-group input-text">
                <label htmlFor="uname">账号</label>
                <input type="text" className="form-control" name="username" id="uname" ref="uname" placeholder="手机号/用户名" value={this.state.userName} onChange={this.changeUsername.bind(this)} />
                  <span className="help-block">{this.state.unameHelp}</span>
              </div>
              ​
              <div className="form-group input-text">
                <label htmlFor="upwd">密码</label>
                <input type="password" className="form-control" name="password" id="upwd" ref="upwd" placeholder="密码" onChange={this.changePassword.bind(this)} />
                  <span className="help-block">{this.state.upwdHelp}</span>
              </div>
              ​
              <div className="form-group">
                <label htmlFor="chk" className="check">
                  <input type="checkbox" id="chk" checked={this.state.isRemember} onClick={this.handleCheckbox.bind(this)} />
                    <span>记住密码</span>
                </label>
              </div>
              ​
              <div className="form-group">
                <button type="button" onClick={this.handleClick.bind(this,this.state.userName,this.state.userPassword)} className="btn btn-primary login-btn">登录</button>
              </div>
            </form>
            ​
          </div>
  )
  }
}

export default withStyles(s)(Login);



